body {
  background-color: #39a069;
  width: 100%;
  height: auto;
  margin: 0px;
  padding: 0px;
  direction: rtl;
}
h1{
  font-size: 60px;

}
.center{
  text-align: center !important;
  color: #f2f8fa;
}
.logo{
  width: 15%;
  height: auto;
  margin: 0px auto;
}
.timeparent {
  width: 100%;
  height: auto;
  text-align: center !important;
}
.time {
  color: white;
  font-size: 70px;
  display: block;
  margin: 25px;
  color: rgb(239, 248, 251);
  text-align: center !important;

}
.timecontainer {
  height: auto;
  width: auto;
  border: 10px solid rgb(239, 248, 251);
  border-radius: 20px;
  width: 50%;
  margin: 0px auto;
  text-align: center !important;
  padding: 10px;


}
.hidden{
  display: none;
}
@media only screen and (max-width: 1024px) {
  table{
    width: 95%;
    padding: 5%;
  }
  .timecontainer {
    height: auto;
    width: auto;
    border: 10px solid rgb(239, 248, 251);
    border-radius: 20px;
    width: 70%;
    text-align: center !important;
    padding: 10px;
  
  
  }
  h1{
    font-size: 40px;
  
  }
}
table{
  margin: 20px auto;
  min-width: 60%;
}
td,th{
  border: 1px solid rgb(245, 245, 81);
}s
th,td{
  padding: 2px;
}
h3{
  font-weight: bold;
}
.orng{
  color: rgb(245, 245, 81);

}
.orng2{
  color: rgb(215, 33, 33);

}
.wht{
  color: rgb(245, 245, 81);
  font-weight: bold;
}
.wht2{
  color: white;
  font-weight: bold;
}
.select{
  width: 50%;
  height: 50px;
  background-color: transparent;
  border: 2px solid rgb(239, 248, 251);
  color: white;
  font: 25px;
  font-weight: bold;
  text-align: center;
}
option{
  background-color: transparent;
  color: black;
}